/**
 * CSS Custom Properties
 *
 * These properties can be overridden using the component's tag as selector.
 *
 * @prop --calcite-input-background-color: defines the background color of the component.
 * @prop --calcite-input-border-color: defines the border color of the component.
 * @prop --calcite-input-button-background-color-hover: defines the background color of a button in the component when it's hovered.
 * @prop --calcite-input-button-background-color: defines the background color of a button in the component.
 * @prop --calcite-input-button-border-color: defines the border color of a button in the component.
 * @prop --calcite-input-button-icon-color-active: defines the icon color of a button in the component when it's active.
 * @prop --calcite-input-button-icon-color-hover: defines the icon color of a button in the component when it's hovered.
 * @prop --calcite-input-button-icon-color: defines the icon color of a button in the component.
 * @prop --calcite-input-corner-radius: defines the corner radius of the component.
 * @prop --calcite-input-date-picker-border-color: defines the border color of the date picker sub component.
 * @prop --calcite-input-date-picker-corner-radius: defines the corner radius of the date picker sub component.
 * @prop --calcite-input-date-picker-day-background-color-hover : defines the background color of a day in the date picker sub component when hovered.
 * @prop --calcite-input-date-picker-day-background-color-selected: defines the background color of a day in the date picker sub component when selected.
 * @prop --calcite-input-date-picker-day-background-color: defines the background color of a day in the date picker sub component.
 * @prop --calcite-input-date-picker-day-corner-radius: defines the corner radius of a day in the date picker sub component.
 * @prop --calcite-input-date-picker-day-range-background-color-hover: defines the background color of day range in the date picker sub component when hovered.
 * @prop --calcite-input-date-picker-day-range-background-color-selected: defines the background color of day range in the date picker sub component when selected.
 * @prop --calcite-input-date-picker-day-text-color-hover: defines the text color of a day in the date picker sub component when hovered.
 * @prop --calcite-input-date-picker-day-text-color: defines the text color of a day in the date picker sub component.
 * @prop --calcite-input-date-picker-day-text-selected: defines the text color of a day in the date picker sub component when selected.
 * @prop --calcite-input-date-picker-header-background-color: defines the background color of the header in the date picker sub component.
 * @prop --calcite-input-date-picker-header-button-background-color : defines the background color of the navigation button in the date picker sub component.
 * @prop --calcite-input-date-picker-header-button-background-color-active: defines the background color of the navigation button in the date picker sub component when active.
 * @prop --calcite-input-date-picker-header-button-background-color-hover : defines the background color of the navigation button in the date picker sub component when hovered.
 * @prop --calcite-input-date-picker-header-buttontext-color-hover: defines the text color of the navigation button in the date picker sub component when hovered.
 * @prop --calcite-input-date-picker-header-buttontext-color: defines the text color of the navigation button in the date picker sub component.
 * @prop --calcite-input-date-picker-header-text-color: defines the text color of the header in the date picker sub component.
 * @prop --calcite-input-date-picker-week-header-text-color : defines the text color of week headers in the date picker sub component.
 * @prop --calcite-input-icon-color:  defines the icon color of the component.
 * @prop --calcite-input-placeholder-text-color: defines the color of placeholder text in the component.
 * @prop --calcite-input-text-color: defines the text color of the component.
 * @prop --calcite-input-toggle-icon-color: defines the component's toggle icon color.
 *
 */

:host {
  @include floatingUIElemAnim(".menu-container");
  @apply relative
    inline-block
    overflow-visible
    align-top
    shadow-none;
  inline-size: var(--calcite-container-size-content-fluid);
}

@function get-trailing-text-input-padding($chevron-spacing) {
  @return calc(var(--calcite-toggle-spacing) + $chevron-spacing);
}

.horizontal-arrow-container,
.vertical-arrow-container {
  calcite-icon {
    --calcite-icon-color: var(--calcite-input-button-icon-color);
  }

  &:hover,
  &:focus {
    calcite-icon {
      --calcite-icon-color: var(--calcite-input-button-icon-color-hover);
    }
  }

  &:active {
    calcite-icon {
      --calcite-icon-color: var(--calcite-input-button-icon-color-active);
    }
  }
}

:host([scale="s"]) {
  --calcite-toggle-spacing: theme("spacing.2");
  --calcite-internal-input-text-input-padding-inline-end: #{get-trailing-text-input-padding(theme("spacing.4"))};
}

:host([scale="m"]) {
  --calcite-toggle-spacing: theme("spacing.3");
  --calcite-internal-input-text-input-padding-inline-end: #{get-trailing-text-input-padding(theme("spacing.6"))};
}

:host([scale="l"]) {
  --calcite-toggle-spacing: theme("spacing.4");
  --calcite-internal-input-text-input-padding-inline-end: #{get-trailing-text-input-padding(theme("spacing.8"))};
}

@include disabled();

.calendar-wrapper {
  @apply shadow-none;
  transform: translate3d(0, 0, 0);
}

.input-wrapper {
  @apply relative;

  .chevron-icon {
    color: var(--calcite-color-text-3);
  }

  &:focus-within,
  &:active,
  &:hover {
    .chevron-icon {
      color: var(--calcite-color-text-1);
    }
  }
}

.toggle-icon {
  @apply absolute flex cursor-pointer items-center;
  inset-inline-end: 0;
  inset-block: 0;
  padding-inline: var(--calcite-toggle-spacing);
  background-color: var(--calcite-input-button-background-color);

  &:hover {
    background-color: var(--calcite-input-button-background-color-hover);
  }

  calcite-icon {
    --calcite-icon-color: var(--calcite-input-icon-color);

    &:hover {
      --calcite-icon-color: var(--calcite-input-icon-color-hover);
    }
    &:active {
      --calcite-icon-color: var(--calcite-input-icon-color-active);
    }
  }
}

:host([range]) {
  .input-container {
    @apply flex;
  }

  .input-wrapper {
    @apply flex-auto;
  }

  .horizontal-arrow-container {
    @apply flex
      items-center
      border
      border-l-0
      border-r-0
      border-solid
      py-0
      px-1;

    background-color: var(--calcite-input-button-background-color, var(--calcite-color-background));
    border-color: var(--calcite-input-border-color, var(--calcite-color-border-input));
  }
}

:host([range][layout="vertical"]) {
  .input-wrapper {
    inline-size: var(--calcite-container-size-content-fluid);
  }

  .input-container {
    @apply flex-col
      items-start;
  }

  .calendar-wrapper--end {
    transform: translate3d(0, 0, 0);
  }

  .vertical-arrow-container {
    inset-block-start: theme("spacing.6");
    @apply absolute
      z-default
      mx-px
      px-2.5;
    background-color: var(--calcite-input-button-background-color, var(--calcite-color-foreground-1));
    inset-inline-start: 0;
  }
}

.menu-container {
  @include floatingUIContainer("var(--calcite-z-index-dropdown)");
  @include floatingUIWrapper();
  @apply invisible;
}

@include floatingUIElemAnim(".menu-container");

:host([open]) .menu-container {
  @include floatingUIWrapperActive();
}

.menu-container--active {
  @include floatingUIWrapperActive();
}

.input .calcite-input__wrapper {
  @apply mt-0;
}

:host([range][layout="vertical"][scale="m"]) .vertical-arrow-container {
  inset-block-start: theme("spacing.6");
  padding-inline-start: theme("spacing.3");

  calcite-icon {
    @apply h-3
      w-3
      min-w-0;
  }
}

:host([range][layout="vertical"][scale="l"]) .vertical-arrow-container {
  inset-block-start: theme("spacing.9");
  @apply px-3.5;
}

:host([range][layout="vertical"][open]) {
  .vertical-arrow-container {
    @apply hidden;
  }
}

@include form-validation-message();
@include hidden-form-input();

.assistive-text {
  @apply sr-only;
}

@include base-component();

calcite-date-picker {
  --calcite-date-picker-border-color: var(--calcite-input-date-picker-border-color);
  --calcite-date-picker-corner-radius: var(--calcite-input-date-picker-corner-radius);
  --calcite-date-picker-day-background-color-hover: var(--calcite-input-date-picker-day-background-color-hover);
  --calcite-date-picker-day-background-color-selected: var(--calcite-input-date-picker-day-background-color-selected);
  --calcite-date-picker-day-background-color: var(--calcite-input-date-picker-day-background-color);
  --calcite-date-picker-day-corner-radius: var(--calcite-input-date-picker-day-corner-radius);
  --calcite-date-picker-day-range-background-color-hover: var(
    --calcite-input-date-picker-day-range-background-color-hover
  );
  --calcite-date-picker-day-range-background-color-selected: var(
    --calcite-input-date-picker-day-range-background-color-selected
  );
  --calcite-date-picker-day-text-color-hover: var(--calcite-input-date-picker-day-text-color-hover);
  --calcite-date-picker-day-text-color: var(--calcite-input-date-picker-day-text-color);
  --calcite-date-picker-day-text-selected: var(--calcite-input-date-picker-day-text-selected);
  --calcite-date-picker-week-header-text-color: var(--calcite-input-date-picker-week-header-text-color);
  --calcite-date-picker-header-background-color: var(--calcite-input-date-picker-header-background-color);
  --calcite-date-picker-header-button-background-color-active: var(
    --calcite-input-date-picker-header-button-background-color-active
  );
  --calcite-date-picker-header-button-background-color-hover: var(
    --calcite-input-date-picker-header-button-background-color-hover
  );
  --calcite-date-picker-header-button-background-color: var(--calcite-input-date-picker-header-button-background-color);
  --calcite-date-picker-header-buttontext-color-hover: var(--calcite-input-date-picker-header-buttontext-color-hover);
  --calcite-date-picker-header-buttontext-color: var(--calcite-input-date-picker-header-buttontext-color);
  --calcite-date-picker-header-text-color: var(--calcite-input-date-picker-header-text-color);
}
